// Activity type styles
const getActivityStyle = (type: string) => {
  switch (type) {
    case 'sale':
      return { icon: 'üí∞', bg: 'bg-emerald-100', text: 'text-emerald-700' }
    case 'lead':
      return { icon: 'üë§', bg: 'bg-blue-100', text: 'text-blue-700' }
    case 'appointment':
      return { icon: 'üìÖ', bg: 'bg-purple-100', text: 'text-purple-700' }
    case 'call':
      return { icon: 'üìû', bg: 'bg-amber-100', text: 'text-amber-700' }
    case 'email':
      return { icon: 'üìß', bg: 'bg-cyan-100', text: 'text-cyan-700' }
    default:
      return { icon: 'üìù', bg: 'bg-gray-100', text: 'text-gray-700' }
  }
}

